import { Injectable } from '@angular/core';
import { AbstractControl } from '@angular/forms';

@Injectable()
export class NgValidatorExtendService {

  constructor() { }

  /**
   * 
   * @return {Function} []
   */
  required(): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      return value ? null : {
        'required': true
      }
    }
  }

  /**
   * 
   * @param  {number}   num 
   * @return {Function}     
   */
  minLength(num: number): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      let valueL = value.length;
      return !value || (valueL >= num) ? null : {
        'minlength': {
          'requiredLength': num,
          'actualLength': valueL
        }
      }
    }
  }

  /**
   * 
   * @param  {number}   num 
   * @return {Function}     
   */
  maxLength(num: number): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      let valueL = value.length;
      return !value || (valueL <= num) ? null : {
        'maxlength': {
          'requiredLength': num,
          'actualLength': valueL
        }
      }
    }
  }

  /**
   * 
   * @param  {number}   num 
   * @return {Function}     
   */
  max(num: number): Function {
    return (ctrl: AbstractControl) => {
      let value = Number(ctrl.value);
      return !value || (value <= num) ? null : {
        'max': {
          'requiredValue': num,
          'actualValue': value
        }
      }
    }
  }

  /**
   * 
   * @param  {number}   num 
   * @return {Function}     
   */
  min(num: number): Function {
    return (ctrl: AbstractControl) => {
      let value = Number(ctrl.value);
      return !value || (value >= num) ? null : {
        'min': {
          'requiredValue': num,
          'actualValue': value
        }
      }
    }
  }

  /**
   * 
   * @param  {number[]} bet 2[2,6]
   * @return {Function}     
   */
  betweenLength(bet: number[]): Function {
    if (bet instanceof Array && bet.length > 1) {
      return (ctrl: AbstractControl) => {
        let value = ctrl.value;
        let valueL = value.length;
        return !value || (valueL >= bet[0] && valueL <= bet[1]) ? null : { 'betweenLength' :{
          'requiredLength': bet[0] + '-' + bet[1], 'actualLength': valueL
        }}
      }
    } else {
      throw new Error(',1');
    }
  }

  /**
   * 
   * @param  {any}    reg   
   * @return {Function}     
   */
  regex(reg: any): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      let regx = new RegExp(reg);
      return !value || (value && regx.test(value)) ? null : {
        'RegExp': regx.toString()
      }
    }
  }

  /**
   * 
   * @return {Function} 
   */
  email(): Function{
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      return !value || /^([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-]*)*\@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])*/.test(value) ? null : {
        'email': true
      }
    }
  }

  /**
   * 
   * @param  {number}   length 
   * @return {Function}        
   */
  length(length:number): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      let valueL = value.length;
      return !value || valueL === Number(length) ? null : {
        'length': {
          'requiredLength': Number(length), 'actualLength': valueL
        }
      }
    }
  }

  /**
   * 
   * @param  {number[]} bet 
   * @return {Function}     
   */
  between(bet:number[]): Function {
    if (bet instanceof Array && bet.length > 1) {
      return (ctrl: AbstractControl) => {
        let value = Number(ctrl.value);
        return !value || (value >= bet[0] && value <= bet[1]) ? null : { 'between' :{
          'requiredValue': bet[0] + '-' + bet[1], 'actualValue': value
        }}
      }
    } else {
      throw new Error(',1');
    }
  }

  /**
   * 
   * @return {Function} 
   */
  integer(): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      return !value || /^\-?\d+$/.test(value) ? null : {
        'integer': true
      }
    }
  }

  /**
   * 
   * @return {Function} 
   */
  number(): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      return !value || !isNaN(Number(value)) ? null : {
        'number': true
      }
    }
  }

  /**
   * 
   * @return {Function} 
   */
  mobile(): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      return !value || /^1\d{10}$/.test(value) ? null : {
        'mobile': true
      }
    }
  }

  /**
   * 
   * @return {Function} 
   */
  telephone(): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      return !value || /^\d{4}\-\d{8}$/.test(value) ? null : {
        'telephone': true
      }
    }
  }

  /**
   * 
   * @return {Function} 
   */
  url(): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      return !value || /(((^https?:(?:\/\/)?)(?:[-;:&=\+\$,\w]+@)?[A-Za-z0-9.-]+|(?:www.|[-;:&=\+\$,\w]+@)[A-Za-z0-9.-]+)((?:\/[\+~%\/.\w-_]*)?\??(?:[-\+=&;%@.\w_]*)#?(?:[\w]*))?)$/g.test(value) ? null : {
        'url': true
      }
    }
  }

  /**
   * 
   * @param  {string}   name 
   * @return {Function}      
   */
  equalTo(name:string): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      if(!ctrl.parent) return null;
      if(!ctrl.parent.controls[name]) throw new Error(''+ name + '')
      let anotherVal = ctrl.parent.controls[name].value || null
      return (anotherVal && value == anotherVal) ? null : {
        'equalTo': {
          'target': name
        }
      }
    }
  }

  /**
   * 
   * @param  {number} num   
   * @return {Function}     
   */
  toFix(num:number): Function {
    return (ctrl: AbstractControl) => {
      let value = ctrl.value;
      let reg = '^([\\d]+)(\\.[\\d]{'+Number(num)+'})?$';
      return new RegExp(reg).test(value) ? null : {
        'toFix': true
      };
    }
  }

  /**
   * , fn(ctrl,opt)
   * @param  {Function} fn  ctrl,opt
   * @param  {any}      opt 
   * @return {Function}     
   */
  selfDefine(fn:Function,opt?:any): Function {
    return (ctrl: AbstractControl) => {
      return fn.call(this,ctrl,opt);
    }
  }
}



